import Divider from '@mui/material/Divider';
import { Typography } from '@mui/material';
import { Box } from '@mui/system';
import Link from '@mui/material/Link';

const HomeView: React.FC = () => {
  return (
    <Box className="main-box">
      <Typography variant='h4' mt={2}> </Typography>
      <Divider />
      <Typography>
        <Box mt={2} component={"span"} display={"block"}>
                 
         &nbsp;
        <Link href='https://swordsandwizardry.co.il/'> </Link>
        .
        </Box>
        <Box component={"span"} display={"block"}>
          ,         .     ,          .
        </Box>
      </Typography>
      <img className='image' src={`${process.env.PUBLIC_URL}/images/home.jpg`} alt="Home Background" />
    </Box>

  );
};

export default HomeView;