import React, { useState } from 'react';
import Box from '@mui/material/Box';
import TextField from '@mui/material/TextField';
import { Button, Typography } from '@mui/material';
import TreasureTable from './GeneratedTreasureTable';
import { calculateItems } from './TreasureCalculator'
import { Item } from '../Item'
import Divider from '@mui/material/Divider';

export default function TreasureCalcView() {
    const [textFieldValue, setTextFieldValue] = useState('');
    const [items, setItems] = useState<null | Item[]>();

    // Update the state on text field change
    const handleTextFieldChange = (event: React.ChangeEvent<HTMLInputElement>) => {
        setTextFieldValue(event.target.value);
    };

    // Add an onClick event handler to the button
    const handleClick = () => {
        setItems(null);
        const experiencePoints = parseInt(textFieldValue);
        let calculatedItems = calculateItems(experiencePoints);
        setItems(calculatedItems);
    };

    return (
        <Box className='main-box'>
            <Box
                className='main-box'
                component="form"
                alignContent='center'
                sx={{
                    '& > :not(style)': { m: 1, width: '100%' },
                }}
                noValidate
                autoComplete="off"
            >

                <TextField
                    id="standard-basic"
                    label=" "
                    variant="standard"
                    value={textFieldValue}
                    type='number'
                    placeholder='  '
                    InputLabelProps={{
                        style: { direction: 'rtl' },
                    }}
                    onChange={handleTextFieldChange} />
                <br />
                <Button

                    variant="contained" onClick={handleClick}></Button>
            </Box>
            {!items &&

                <Box id='description'>
                    <Box>
                        <Typography align='center' variant='h5'>
                                ,      
                        </Typography>
                        <Divider />
                    </Box>
                    <Box component="span" display="block">
                        <Typography variant='body1' >
                                     64
                        </Typography>
                    </Box >
                    <Box component="span" display="block">
                        <Typography variant='body1' sx={{ fontStyle: 'italic' }}>
                            "               "  13 + 1.      .          ,         .
                             ,   100    ,  110.    10,        ,        .  20     1-19,          .    20,        "
                            <Box component="span" display="block">
                                " ,     ,    10    1000     ,    10,      ,       "
                            </Box>
                            <Box component="span" display="block">
                                "    10  20.    1  19          .    20,        ."
                            </Box>
                        </Typography>
                    </Box>
                </Box>
            }
            {items && <TreasureTable items={items} />}

        </Box>
    );
}


